.fc-day-has-res .fc-daygrid-day-number::after {
  content: '';
  display: inline-block;
  width: 6px;
  height: 6px;
  background: #1e88e5;
  border-radius: 50%;
  margin-left: 6px;
}

.fc-day-badge {
  background: #1e88e5;
  color: #fff;
  border-radius: 10px;
  font-size: 10px;
  line-height: 1;
  padding: 2px 6px;
  margin-left: 6px;
}
/* üå∏ „Ç´„É¨„É≥„ÉÄ„Éº„Ç≥„É≥„Éù„Éº„Éç„É≥„Éà - „Éû„ÉÜ„É™„Ç¢„É´„Éá„Ç∂„Ç§„É≥ */
.calendar-container {
  position: relative;
  width: 100%;
  /* Ë¶™„ÅÆÈ´ò„Åï„Å´‰æùÂ≠ò„Åó„Å™„ÅÑ„Çà„ÅÜauto„Å´„Åó„Å¶„Éö„Éº„Ç∏ÂÖ®‰Ωì„Åß„Çπ„ÇØ„É≠„Éº„É´ */
  height: auto;
  min-height: 0;
}

.calendar-loading {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1000;
  background: var(--color-surface);
  padding: var(--spacing-lg);
  border-radius: var(--radius-medium);
  box-shadow: var(--elevation-3);
  font-size: 16px;
  color: var(--color-on-surface);
}

.calendar-error {
  position: absolute;
  top: var(--spacing-lg);
  left: var(--spacing-lg);
  right: var(--spacing-lg);
  z-index: 1000;
  background: var(--color-error-light);
  color: var(--color-error);
  border: 1px solid var(--color-error);
  padding: var(--spacing-md);
  border-radius: var(--radius-medium);
  box-shadow: var(--elevation-1);
  font-size: 14px;
  line-height: 1.4;
}

/* „ÉÑ„Éº„É´„Éê„Éº: „Ç§„É≥„É©„Ç§„É≥„Çπ„Çø„Ç§„É´„ÅÆÂ§ñÂá∫„Åó */
.calendar-toolbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 8px;
}

.calendar-view-switch {
  display: inline-flex;
  gap: 6px;
  background: var(--color-surface);
  border: 1px solid var(--color-outline);
  border-radius: var(--radius-medium);
  padding: 4px;
}

.view-switch-button {
  background: transparent;
  border: none;
  color: var(--color-on-surface-variant);
  font-size: 0.85em;
  padding: 6px 12px;
  border-radius: var(--radius-small);
  cursor: pointer;
  transition: all 0.2s ease;
}

.view-switch-button:hover {
  background: var(--color-surface-variant);
  color: var(--color-on-surface);
}

.view-switch-button.is-active {
  background: var(--color-primary);
  color: var(--color-on-primary);
}

.calendar-toolbar .mine-label {
  font-size: 0.9em;
}

.calendar-toolbar .mine-checkbox {
  margin-left: 6px;
}

/* FullCalendar „ÅÆ„Éû„ÉÜ„É™„Ç¢„É´„Éá„Ç∂„Ç§„É≥Ë™øÊï¥ */
.fc {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', sans-serif;
  background: var(--color-surface);
  border-radius: var(--radius-medium);
  box-shadow: var(--elevation-1);
  /* Â∞è„Åï„ÅÑÁîªÈù¢„ÅßÂÜÖÂÆπ„ÅåÂàá„ÇäÂèñ„Çâ„Çå„Å™„ÅÑ„Çà„ÅÜ„Å´„Åô„Çã */
  overflow: visible;
}

.fc-toolbar-title {
  font-size: 1.5em;
  font-weight: 600;
  color: var(--color-on-surface);
}

/* „ÉÑ„Éº„É´„Éê„Éº„ÅÆ„Éú„Çø„É≥ÈñìÈöîË™øÊï¥ */
.fc-toolbar {
  padding: var(--spacing-md) var(--spacing-lg);
  gap: var(--spacing-lg);
}

.fc-toolbar-chunk {
  display: flex;
  align-items: center;
  gap: var(--spacing-md);
}

/* „Éä„Éì„Ç≤„Éº„Ç∑„Éß„É≥„Éú„Çø„É≥„ÅÆÈñìÈöî„ÇíË™øÊï¥ */
.fc-button-group .fc-button {
  margin: 0 var(--spacing-xs);
}

.fc-button-group .fc-button:first-child {
  margin-left: 0;
}

.fc-button-group .fc-button:last-child {
  margin-right: 0;
}

.fc-button {
  background-color: var(--color-surface);
  border-color: var(--color-outline);
  color: var(--color-on-surface);
  box-shadow: none;
}

.fc-button:hover {
  background-color: var(--color-surface-variant);
  border-color: var(--color-outline-variant);
  color: var(--color-on-surface);
}

.fc-button:focus { box-shadow: var(--focus-ring); }

/* Segmented active state */
.fc-button.fc-button-active,
.fc-button:active {
  background-color: var(--color-primary);
  border-color: var(--color-primary);
  color: var(--color-on-primary);
}

/* Segment look */
.fc-button-group .fc-button { border-radius: 6px; }

.fc-event {
  border-radius: var(--radius-small);
  padding: var(--event-padding, 2px 4px);
  font-size: var(--event-font-size, 11px);
  line-height: 1.2;
  overflow: hidden;
  cursor: pointer;
  transition: all 0.2s ease;
}

.fc-event:hover {
  transform: translateY(-1px);
  box-shadow: var(--elevation-1);
}

.fc-timegrid-slot-label {
  font-size: 0.85em;
  color: var(--color-on-surface-variant);
}

.fc-timegrid-slot {
  border-color: var(--color-outline-variant);
}

/* === ÂúüÊó•„ÅÆ„Éò„ÉÉ„ÉÄ„Éº„Ç´„É©„ÉºÔºàË¶ñË™çÊÄßÂêë‰∏äÔºâ === */
.fc-col-header-cell {
  background-color: var(--color-surface-variant);
  color: var(--color-on-surface);
}

/* Êó¢Â≠ò„ÅÆ day „ÇØ„É©„ÇπÊåáÂÆö„ÅØÊÆã„Åô */
.fc-day-sat {
  background-color: var(--color-saturday-light);
}

.fc-day-sun {
  background-color: var(--color-sunday-light);
}

.fc-daygrid-day-number {
  color: var(--color-on-surface);
  font-weight: 500;
}

.fc-today {
  background-color: var(--color-primary-50) !important;
}

.fc-now-indicator {
  border-color: var(--color-primary);
  border-width: 2px;
}

/* ÈÄ±Êú´„Çπ„Çø„Ç§„É´ÂÜçÂÆöÁæ©: ÂúüÊõú=ÈùíÁ≥ªÂº∑Ë™ø, Êó•Êõú=Ëµ§Á≥ªÂº∑Ë™ø */
:root {
  --sat-bg: #e3f2ff;
  --sat-text: #0d60c7;
  --sun-bg: #ffe4e6;
  --sun-text: #d3002f;
}

/* ‚ñº ‰∏çË¶Å„Å´„Å™„Å£„Åü nth-child „Éô„Éº„Çπ„ÅÆÂÜçÂÆöÁæ©„ÅØÂâäÈô§ÔºàtimeGrid „ÅßÂàó„Åö„Çå„ÇíËµ∑„Åì„Åô„Åü„ÇÅÔºâ
.fc-col-header-cell:nth-child(1) { 
  background: var(--sun-bg) !important;
  color: var(--sun-text) !important;
  font-weight: 600;
}
.fc-col-header-cell:nth-child(7) { 
  background: var(--sat-bg) !important;
  color: var(--sat-text) !important;
  font-weight: 600;
}
*/

/* === Weekend header override (class based) === */
.fc-col-header-cell.fc-day-sun { background: var(--sun-bg) !important; color: var(--sun-text) !important; font-weight: 600; }
.fc-col-header-cell.fc-day-sat { background: var(--sat-bg) !important; color: var(--sat-text) !important; font-weight: 600; }
.fc-col-header-cell.fc-day-sun .fc-col-header-cell-cushion,
.fc-col-header-cell.fc-day-sat .fc-col-header-cell-cushion { color: inherit !important; }
/* Ensure day view (single column) also applies */
.fc-timegrid-col.fc-day-sun, .fc-timegrid-col.fc-day-sat { background: inherit; }

/* Êó•‰ªò„Çª„É´Êú¨‰ΩìËÉåÊôØ (dayGrid) */
.fc-day-sun { background: var(--sun-bg) !important; }
.fc-day-sat { background: var(--sat-bg) !important; }

/* Êó•ÊõúÊï∞Â≠ó„ÇíËµ§ / ÂúüÊõúÊï∞Â≠ó„ÇíÈùí */
.fc-day-sun .fc-daygrid-day-number { color: var(--sun-text) !important; }
.fc-day-sat .fc-daygrid-day-number { color: var(--sat-text) !important; }

/* timeGrid (ÈÄ±/Êó•) „ÅÆÂàóËÉåÊôØ */
.fc-timegrid-col.fc-day-sun { background: var(--sun-bg) !important; }
.fc-timegrid-col.fc-day-sat { background: var(--sat-bg) !important; }

/* Êóß: ÈÄ±Êú´„ÅÆ‰ªäÊó•Âº∑Ë™ø (inset box-shadow) -> „É¨„Ç§„Ç¢„Ç¶„Éà„Åö„Çå„ÅÆ„Åü„ÇÅÁΩÆÊèõ
.fc-day-sun.fc-day-today, .fc-day-sat.fc-day-today { box-shadow: inset 0 0 0 2px #ff9800; }
*/

/* === ‰ªäÊó•„Éè„Ç§„É©„Ç§„ÉàÂÜçË™øÊï¥ (Week „ÅÆÊ¨†„ÅëËß£Ê∂à) === */
/* Áõ¥Ââç„ÅßÂ∞éÂÖ•„Åó„Åü border ÊñπÂºè„ÇíÊí§Âõû„ÅóÊì¨‰ººË¶ÅÁ¥†ÊñπÂºè„Å´Êàª„Åô */
.fc-col-header-cell.fc-day-today,
.fc-timegrid-col.fc-day-today { position: relative; }

/* „Éò„ÉÉ„ÉÄ„Éº: ‰∏ãÊñπÂêë„Å∏ 2px „Ç™„Éº„Éê„Éº„É©„ÉÉ„Éó */
.fc-col-header-cell.fc-day-today::after {
  content: '';
  position: absolute;
  left: 0; right: 0; top: 0; bottom: -2px; /* ‰∏ã„Å∏Âª∂Èï∑ */
  border: 2px solid #ff9800;
  border-bottom: 0; /* ‰∏ã„ÅØ„Éú„Éá„Ç£ÂÅ¥„ÅåÊèè„Åè */
  pointer-events: none;
  box-sizing: border-box;
  border-radius: 2px 2px 0 0;
  z-index: 5;
}
/* „Éú„Éá„Ç£Âàó: ‰∏äÊñπÂêë„Å∏ 2px „Ç™„Éº„Éê„Éº„É©„ÉÉ„Éó */
.fc-timegrid-col.fc-day-today::after {
  content: '';
  position: absolute;
  left: 0; right: 0; top: -2px; bottom: 0; /* ‰∏ä„Å∏Âª∂Èï∑ */
  border: 2px solid #ff9800;
  border-top: 0; /* ‰∏ä„ÅØ„Éò„ÉÉ„ÉÄ„ÉºÂÅ¥„ÅåÊèè„Åè */
  pointer-events: none;
  box-sizing: border-box;
  border-radius: 0 0 2px 2px;
  z-index: 4;
}
/* dayGrid (Êúà) „ÅØ‰∏ÄÊûö„Çª„É´„Å™„ÅÆ„Åß outline Á∂≠ÊåÅ */
.fc-daygrid-day.fc-day-today { outline:2px solid #ff9800; outline-offset:-2px; }

/* „É¢„Éê„Ç§„É´Ë°®Á§∫Ë™øÊï¥ */
.is-mobile-cal .fc-col-header-cell-cushion {
  font-size: 11px;
  padding: 2px 0;
}

.is-mobile-cal .fc-daygrid-day-number {
  font-size: 11px;
  padding: 2px 4px;
}

.is-mobile-cal .fc-toolbar-title {
  font-size: 1.1em;
}

.is-mobile-cal .fc-daygrid-day-frame {
  min-height: 58px;
}

/* „É¨„Çπ„Éù„É≥„Ç∑„ÉñÂØæÂøú */
@media (max-width: 768px) {
  .fc-toolbar {
    flex-direction: column;
    gap: 10px;
  }
  
  .fc-toolbar-chunk {
    display: flex;
    justify-content: center;
  }
  
  .calendar-toolbar {
    flex-direction: column;
    gap: 6px;
    align-items: flex-start;
  }

  .calendar-view-switch {
    align-self: stretch;
    justify-content: space-between;
  }

  .fc-button {
    font-size: 0.8em;
    padding: 4px 8px;
  }
  
  .legend-items {
    justify-content: center;
  }
  
  .legend-item {
    font-size: 10px;
  }
}

/* ÁîªÈù¢È´ò„Åï„Åå‰Ωé„ÅÑÁ´ØÊú´Âêë„Åë„ÅÆË™øÊï¥ */
@media (max-height: 800px) {
  .fc-toolbar { padding: 8px 12px; }
  .fc-toolbar-title { font-size: 1.2em; }
  .is-mobile-cal .fc-daygrid-day-frame { min-height: 50px; }
}

/* === ÊïôÂÆ§„Ç´„ÉÜ„Ç¥„É™„Åî„Å®„ÅÆÈÖçËâ≤ === */
.room-cat-small  { background:#10B981 !important; border-color:#0E946C !important; color:#fff !important; }
.room-cat-large  { background:#F59E0B !important; border-color:#C47E08 !important; color:#111 !important; }
