.room-cat-blue   { background:#3B82F6 !important; border-color:#2563EB !important; color:#fff !important; }
.room-cat-red    { background:#EF4444 !important; border-color:#DC2626 !important; color:#fff !important; }
.room-cat-default{ background:#9CA3AF !important; border-color:#6B7280 !important; color:#fff !important; }

@media (max-height: 700px) {
  .fc-toolbar { padding: 6px 10px; }
  .fc-toolbar-title { font-size: 1.05em; }
  .fc-button { font-size: 0.8em; padding: 3px 8px; }
}

/* ‰∏äÈôêË∂ÖÈÅé„ÅÆÁÑ°ÂäπÊó•„ÇíË¶ñË¶öÁöÑ„Å´ËñÑ„Åè */
.fc .fc-daygrid-day.fc-day-overlimit,
.fc .fc-timegrid-col.fc-day-overlimit {
  opacity: 0.45;
}
