/* üå∏ Ê°úÂíåÈ´òÊ†°ÊïôÂÆ§‰∫àÁ¥Ñ„Ç∑„Çπ„ÉÜ„É† - „Çπ„ÇØ„Éº„É´„Éñ„É©„É≥„Éâ„ÉÜ„Éº„Éû */
:root {
  /* === „Ç´„É©„Éº„Çπ„Ç≠„Éº„É† C: „Éï„É©„ÉÉ„ÉàÊ∑°Ëâ≤ + Âº∑Ë™ø„Ç¢„ÇØ„Çª„É≥„Éà === */
  --color-primary: #2d7fd3;            /* „Ç¢„ÇØ„Ç∑„Éß„É≥‰∏ªËâ≤ (Êóßprimary„ÇíÂÜçÂÆöÁæ©) */
  --color-primary-light: #e3f3ff;      /* Ê∑°ËÉåÊôØÁî® */
  --color-primary-dark: #1f5c96;       /* „Éõ„Éê„Éº/Âº∑Ë™ø */
  --color-on-primary: #ffffff;
  --color-brand-text: #1f3047;         /* „Çø„Ç§„Éà„É´/‰∏ªË¶Å„ÉÜ„Ç≠„Çπ„ÉàËâ≤ */
  --color-accent: #2d7fd3;             /* ÂêåÁæ© (‰∫íÊèõÁõÆÁöÑ) */
  --color-accent-hover: #256ab2;
  --color-accent-alt: #e48daa;         /* Ê°ú„Ç¢„ÇØ„Çª„É≥„Éà */
  --color-accent-alt-light: #fde8ef;

  --color-surface: #ffffff;
  --color-surface-alt: #f0f4f8;        /* „Çª„ÇØ„Ç∑„Éß„É≥ËÉåÊôØ */
  --color-background: #f5f7fa;         /* ÂÖ®‰ΩìËÉåÊôØ„Çí„ÇÑ„ÇÑÈùí„Åø */
  --color-surface-variant: #fafbfc;

  --color-on-surface: #212a33;
  --color-on-surface-variant: #5a6773;

  --color-outline: #d9e1e9;            /* ËñÑ„ÅÑÂ¢ÉÁïåÁ∑ö */
  --color-outline-variant: #c3ced9;    /* ÊøÉ„ÅÑ„ÇÅ */
  --color-border-strong: #b4c2cf;

  --color-focus: #497ddf;
  --focus-ring: 0 0 0 3px rgba(73,125,223,0.45);

  /* ÈÄ±Êú´Ëâ≤ (Êéß„Åà„ÇÅ) */
  --color-saturday: #2d7fd3;
  --color-saturday-light: #e8f2fb;
  --color-sunday: #d55a64;
  --color-sunday-light: #fae9ec;

  /* Áä∂ÊÖãËâ≤ÂÜçË™øÊï¥ */
  --color-success: #2f7d4b;
  --color-success-light: #e6f6ec;
  --color-warning: #cc8a0e;
  --color-warning-light: #fdf1da;
  --color-error: #c9373a;
  --color-error-light: #fde6e7;
  --color-info: #2f6fa0;
  --color-info-light: #e2f1fb;

  /* === „Ç∑„É£„Éâ„Ç¶ === */
  --elevation-1: 0 1px 2px rgba(0,0,0,0.08), 0 1px 1px rgba(0,0,0,0.06);
  --elevation-2: 0 4px 8px rgba(0,0,0,0.10), 0 2px 4px rgba(0,0,0,0.08);
  --elevation-3: 0 10px 20px rgba(0,0,0,0.12), 0 4px 8px rgba(0,0,0,0.10);
  --elevation-4: 0 14px 28px rgba(0,0,0,0.16), 0 10px 10px rgba(0,0,0,0.10);
  --elevation-header: 0 2px 4px rgba(0,0,0,0.06), 0 1px 2px rgba(0,0,0,0.04);

  /* === Ëßí‰∏∏ / „Çπ„Éö„Éº„Ç∑„É≥„Ç∞ === */
  --radius-small: 6px;
  --radius-medium: 10px;
  --radius-large: 14px;
  --radius-extra-large: 18px;
  --spacing-xs: 4px;
  --spacing-sm: 8px;
  --spacing-md: 16px;
  --spacing-lg: 24px;
  --spacing-xl: 32px;
  --spacing-xxl: 48px;

  /* === „Éà„É©„É≥„Ç∏„Ç∑„Éß„É≥Ôºà„Ç¢„Éã„É°„Éº„Ç∑„Éß„É≥Ôºâ === */
  --transition-fast: 150ms cubic-bezier(0.4, 0, 0.2, 1);
  --transition-normal: 250ms cubic-bezier(0.4, 0, 0.2, 1);
  --transition-slow: 350ms cubic-bezier(0.4, 0, 0.2, 1);
  --ease-in-out: cubic-bezier(0.4, 0, 0.2, 1);
  --ease-out: cubic-bezier(0, 0, 0.2, 1);
  --ease-in: cubic-bezier(0.4, 0, 1, 1);

  /* === Breakpoints === */
  --bp-mobile: 600px;
  --bp-tablet: 900px;
  --bp-desktop: 1200px;

  /* === „Éò„ÉÉ„ÉÄ„Éº/„Éï„ÉÉ„Çø„ÉºËÉåÊôØËâ≤ (Navy Scheme) === */
  --color-header-bg: #eef3f8; /* ËñÑ„ÅÑÁ¥∫„Éà„Éº„É≥ (ÊòéÂ∫¶UP) */
  --color-header-border: #d3dee7; /* Ê∑°Â¢ÉÁïå */
  --color-header-text: #1f3047; /* „ÉÄ„Éº„ÇØ„ÉÜ„Ç≠„Çπ„Éà */
  --color-footer-bg: #e9f0f6; /* „Éï„ÉÉ„Çø„Éº„ÇÑ„ÇÑÊøÉ„ÅÑ„ÇÅ */
  --color-footer-border: #c9d6e0;
}

/* „É¢„Éê„Ç§„É´Â∞ÇÁî®„Éò„É´„Éë„Éº (JS‰∏çË¶Å„ÅßÁ∞°ÊòìÈùûË°®Á§∫Âà∂Âæ°) */
.only-desktop { display: block; }
.only-mobile { display: none; }

@media (max-width: 599px) {
  .only-desktop { display: none !important; }
  .only-mobile { display: block !important; }
}

body {
  margin: 0;
  /* Prefer Noto Sans JP for consistent JP rendering on Windows/macOS */
  font-family: 'Noto Sans JP', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', Arial,
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-variant-numeric: tabular-nums;
  font-feature-settings: 'tnum' 1, 'lnum' 1;
  background-color: var(--color-background);
  color: var(--color-on-surface);
  font-size: 16px; /* WindowsÂêë„Åë„Éô„Éº„Çπ„Éï„Ç©„É≥„Éà„Çµ„Ç§„Ç∫ */
}

/* 1366x768Âêë„Åë: „Éô„Éº„Çπ„Éï„Ç©„É≥„Éà„Çµ„Ç§„Ç∫„ÇíÊúÄÈÅ©Âåñ */
@media (max-width: 1440px) and (max-height: 900px) {
  body {
    font-size: 15px;
  }
}

/* „Çà„ÇäÂ§ß„Åç„ÅÑÁîªÈù¢„Åß„ÅØ„Éï„Ç©„É≥„Éà„Çµ„Ç§„Ç∫„ÇíÂ¢ó„ÇÑ„Åô */
@media (min-width: 1600px) {
  body {
    font-size: 17px;
  }
}

@media (min-width: 1920px) {
  body {
    font-size: 18px;
  }
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* „Éï„Ç©„Éº„Ç´„ÇπÂèØË¶ñÂåñ */
:focus-visible {
  outline: none;
  box-shadow: var(--focus-ring);
  border-radius: 4px;
}

/* „É¶„Éº„ÉÜ„Ç£„É™„ÉÜ„Ç£„ÇØ„É©„Çπ„ÅÆ„Ç§„É≥„Éù„Éº„Éà */
@import './styles/utilities.css';
