/* „Ç®„ÇØ„Çª„É´È¢®Êó•Âà•‰∫àÁ¥ÑË°®Á§∫„ÉÜ„Éº„Éñ„É´ */
.daily-reservation-table {
  width: 100%;
  background: #ffffff; /* Êó¢„Å´ÁôΩ */
  border: 1px solid #d4dbe2; /* Áµ±‰∏Ä„É©„Ç§„É≥Ëâ≤ */
  border-radius: var(--radius-medium);
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', sans-serif;
  font-size: 13px;
  overflow: hidden; /* Ê®™„Çπ„ÇØ„É≠„Éº„É´ÊäëÊ≠¢ */
  box-shadow: 0 1px 2px rgba(0,0,0,0.06); /* ÂΩ±„Çè„Åö„Åã„Å´Âº±„ÇÅ */
  /* „É¨„Çπ„Éù„É≥„Ç∑„ÉñÂØæÂøú„ÅÆ„Åü„ÇÅÊúÄÂ∞èÂπÖ„ÇíË®≠ÂÆö */
  min-width: 0;
  box-sizing: border-box;
}

/* „Éï„Ç£„É´„Çø„ÉºË°å */
.daily-reservation-table .filters {
  display: flex;
  gap: 8px;
  margin: 8px 12px;
}

.daily-reservation-table .filters label select {
  margin-left: 6px;
}

.daily-reservation-table * {
  box-sizing: border-box;
}

/* „ÉÜ„Éº„Éñ„É´Ê®™„Çπ„ÇØ„É≠„Éº„É´ÂØæÂøú„ÅÆ„Ç≥„É≥„ÉÜ„Éä */
.table-scroll-container {
  overflow-x: hidden; /* Ê®™„Çπ„ÇØ„É≠„Éº„É´ÁÑ°ÂäπÂåñ */
  overflow-y: hidden;
  width: 100%;
  /* „Éò„ÉÉ„ÉÄ„ÉºÂõ∫ÂÆö„ÅÆ„Åü„ÇÅÈ´ò„ÅïÂà∂Èôê„ÇíËß£Èô§ */
  max-height: none;
  border: none; /* ‰∫åÈáç„Éú„Éº„ÉÄ„ÉºÂõûÈÅø„ÅßÂπÖË∂ÖÈÅéÈò≤Ê≠¢ */
}

/* „Éò„ÉÉ„ÉÄ„ÉºÂõ∫ÂÆöÁî®„ÅÆ„ÉÜ„Éº„Éñ„É´„É©„ÉÉ„Éë„Éº */
.table-wrapper {
  position: relative;
  max-height: 192px; /* „Éò„ÉÉ„ÉÄ„Éº(32px) + 5Ë°åÂàÜ(160px) */
  overflow-y: auto;
  overflow-x: hidden !important; /* Âøµ„ÅÆ„Åü„ÇÅ */
}

/* „Éò„ÉÉ„ÉÄ„Éº */
.table-header {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 12px;
  padding: 8px 12px;
  background: #ffffff; /* Êóß #f6f8fa -> „Éõ„ÉØ„Ç§„ÉàÂåñ */
  border-bottom: 1px solid #d0d7de; /* Â§ñÊû†„Å®ÂêåËâ≤ */
  position: sticky; /* „Éó„É¨„Éì„É•„Éº: „Éò„ÉÉ„ÉÄ„ÉºÂõ∫ÂÆö */
  top: 0;
  z-index: 20;
}

.table-header h4 {
  margin: 0;
  color: #24292f;
  font-size: 13px;
  font-weight: 600;
}

.loading-indicator {
  color: #0969da;
  font-size: 11px;
}

.error-message {
  color: #d1242f;
  font-size: 11px;
}

/* „Éò„ÉÉ„ÉÄ„ÉºÁõ¥‰∏ã„ÅÆ„Ç§„É≥„É©„Ç§„É≥„É°„ÉÉ„Çª„Éº„Ç∏‰ΩôÁôΩ */
.loading-inline,
.error-inline,
.no-reservations-inline {
  padding: 0 12px 6px;
}

/* „Çµ„Éñ„Çø„Éñ */
.subtabs { display:flex; gap:8px; }
.tabs-padding { padding: 0 12px 6px; }
.subtabs .tab {
  background:#fff; border:1px solid #d0d7de; color:#24292f; padding:4px 10px; border-radius:6px; cursor:pointer; font-size:12px;
}
.subtabs .tab.active { background:#0969da; color:#fff; border-color:#0969da; }

/* „Ç®„ÇØ„Çª„É´È¢®„ÉÜ„Éº„Éñ„É´ */
.excel-table {
  width: 100% !important;
  min-width: 0 !important; /* Âº∑Âà∂Âõ∫ÂÆö */
  border-collapse: separate; /* collapse „Çí‰Ωø„Çè„ÅöÈáçË§áÂõûÈÅø */
  border-spacing: 0; /* ‰ΩôÁôΩ„Å™„Åó */
  font-size: 12px;
  background: white;
  table-layout: fixed; /* ÂàóÂπÖÂõ∫ÂÆö„Åó„Å¶ÊäòËøî„Åó */
  border: none;
}

.excel-table thead th {
  background: #ffffff; /* Êóß #f6f8fa -> „Éõ„ÉØ„Ç§„ÉàÂåñ */
  color: #24292f;
  font-weight: 600;
  padding: 8px 12px;
  text-align: center; /* ‰∏≠Â§ÆÊèÉ„Åà */
  border: none; /* Êó¢Â≠ò„ÇØ„É™„Ç¢ */
  border-right: 1px solid #d4dbe2;
  border-bottom: 1px solid #d4dbe2;
  font-size: 12px;
  white-space: nowrap;
  /* „Éò„ÉÉ„ÉÄ„ÉºÂõ∫ÂÆö */
  position: sticky;
  top: 0;
  z-index: 10;
}

.excel-table tbody td {
  padding: 6px 12px;
  border: none; /* Êó¢Â≠ò„ÇØ„É™„Ç¢ */
  border-right: 1px solid #d4dbe2;
  border-bottom: 1px solid #d4dbe2;
  vertical-align: middle; /* ‰∏≠Â§ÆÊèÉ„Åà */
  text-align: center; /* ‰∏≠Â§ÆÊèÉ„Åà */
  background: white;
  font-size: 12px;
  white-space: normal; /* Êäò„ÇäËøî„ÅóË®±ÂèØ */
  word-break: break-word;
}

.excel-table tbody tr:nth-child(even) td {
  background: #f6f8fa;
}

.excel-table tbody tr:hover td {
  background: #ddf4ff !important;
}

/* ‰ΩôÂàÜ„Å™‰∫åÈáçÁ∑öÈô§Âéª */
.excel-table tbody tr:first-child td { border-top: none; }
.excel-table tbody td:first-child { border-left: none; }

/* ÊúÄÁµÇÂàó„ÅØÂè≥Á∑ö„Å™„Åó (Â§ñÊû†„ÅåÊãÖÂΩì) */
.excel-table thead th:last-child,
.excel-table tbody td:last-child { border-right: none; }

/* ÊúÄÁµÇË°å„ÅØ‰∏ãÁ∑ö„Å™„Åó (Â§ñÊû†„ÅåÊãÖÂΩì) */
.excel-table tbody tr:last-child td { border-bottom: none; }

/* ÂàóÂπÖË®≠ÂÆö */
.col-room { width: 12%; min-width: 0; font-weight: 500; color: #24292f; }
.col-period { width: 10%; min-width: 0; text-align: center; font-weight: 500; color: #0969da; }
.col-time { width: 18%; min-width: 0; text-align: center; font-family: 'SF Mono', 'Monaco', 'Inconsolata', 'Roboto Mono', monospace; color: #656d76; }
.col-title { width: 38%; min-width: 0; color: #24292f; }
.col-user { width: 14%; min-width: 0; color: #656d76; }
.col-actions { width: 8%; min-width: 72px; }

/* „Çª„É´ÂÜÖÂÆπ */
.room-name { font-weight: 500; color: #24292f; }
.period-badge { background: #ddf4ff; color: #0969da; padding: 2px 6px; border-radius: 12px; font-size: 11px; font-weight: 600; display: inline-block; }
.time-range { font-family: 'SF Mono', 'Monaco', 'Inconsolata', 'Roboto Mono', monospace; color: #656d76; font-size: 13.5px; font-variant-numeric: tabular-nums; font-feature-settings: 'tnum' 1, 'lnum' 1; font-weight: 600; letter-spacing: 0.2px; }
.reservation-title {
  color: #24292f;
  font-weight: 500;
  line-height: 1.4;
  font-size: 13.5px; /* Ë¶ñË™çÊÄß„Ç¢„ÉÉ„ÉóÔºàWin/Mac‰∏°ÂØæÂøúÔºâ */
  font-family: 'Noto Sans JP', 'Hiragino Sans', 'Hiragino Kaku Gothic ProN', 'Meiryo', 'Segoe UI', -apple-system, BlinkMacSystemFont, 'Roboto', 'Helvetica Neue', Arial, sans-serif;
}
.reservation-user { color: #656d76; font-size: 13.5px; }

.inline-delete-btn { background:#fff; border:1px solid #d1242f; color:#d1242f; padding:2px 6px; border-radius:6px; cursor:pointer; font-size:10px; }
.inline-delete-btn:hover { background:#d1242f; color:#fff; }
.inline-confirm { display:flex; gap:6px; justify-content:center; }
.inline-confirm .confirm { background:#d1242f; color:#fff; border:none; padding:2px 6px; border-radius:6px; font-size:10px; cursor:pointer; }
.inline-confirm .cancel { background:#fff; border:1px solid #d0d7de; padding:2px 6px; border-radius:6px; font-size:10px; cursor:pointer; }

/* „É¨„Çπ„Éù„É≥„Ç∑„ÉñÂØæÂøú */
@media (max-width: 768px) {
  .daily-reservation-table { font-size: 10px; margin: 0; border-radius: 4px; }
  .table-header { padding: 6px 10px; }
  .table-header h4 { font-size: 12px; }
  .excel-table { font-size: 10px; min-width: 0 !important; width: 100% !important; }
  .excel-table thead th { padding: 6px 4px; font-size: 10px; }
  .excel-table tbody td { padding: 4px 4px; font-size: 10px; }
  .col-room { width: 14%; }
  .col-period { width: 12%; }
  .col-time { width: 20%; }
  .col-user { width: 16%; }
  .col-title { width: 34%; }
}

@media (max-width: 480px) {
  .daily-reservation-table { border-radius: 2px; }
  .table-header { padding: 4px 8px; }
  .table-header h4 { font-size: 11px; }
  .excel-table { font-size: 9px; min-width: 0 !important; width: 100% !important; }
  .excel-table thead th { padding: 4px 2px; font-size: 9px; }
  .excel-table tbody td { padding: 3px 2px; font-size: 9px; }
  .col-room { width: 16%; }
  .col-period { width: 14%; }
  .col-time { width: 22%; }
  .col-user { width: 18%; }
  .col-title { width: 30%; }
}

/* Âç∞Âà∑ÂØæÂøú */
@media print {
  .daily-reservation-table { box-shadow: none; border: 1px solid #000; font-size: 10px; }
  .table-header { background: white !important; border-bottom: 1px solid #000; -webkit-print-color-adjust: exact; print-color-adjust: exact; }
  .excel-table thead th,
  .excel-table tbody td { border-right: 1px solid #000 !important; border-bottom: 1px solid #000 !important; }
  .excel-table thead th:last-child,
  .excel-table tbody td:last-child { border-right: none !important; }
  .excel-table tbody tr:last-child td { border-bottom: none !important; }
}

.reservation-sheet .excel-table { width: 100% !important; table-layout: fixed; min-width: 0; }
.reservation-sheet .excel-table thead th,
.reservation-sheet .excel-table tbody td { overflow: hidden; text-overflow: ellipsis; }
