/* „É¶„Éº„ÉÜ„Ç£„É™„ÉÜ„Ç£„ÇØ„É©„Çπ - ÂÖ±ÈÄö„Çπ„Çø„Ç§„É´ */

/* === „Éú„Çø„É≥ÂÖ±ÈÄö„Çπ„Çø„Ç§„É´ === */
.btn {
  padding: 10px 16px;
  border: 1px solid transparent;
  border-radius: 6px;
  cursor: pointer;
  font-size: 14px;
  font-weight: 500;
  transition: all var(--transition-normal);
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
}

.btn:hover:not(:disabled) {
  transform: translateY(-1px);
  box-shadow: var(--elevation-2);
}

.btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

/* „Éú„Çø„É≥„Éê„É™„Ç®„Éº„Ç∑„Éß„É≥ */
/* Primary: ‰∏ªË¶Å„Ç¢„ÇØ„Ç∑„Éß„É≥Ôºà‰∫àÁ¥Ñ‰ΩúÊàêÁ≠âÔºâ */
.btn-primary {
  background: var(--color-primary);
  color: var(--color-on-primary);
  font-weight: 600;
  padding: 12px 24px;
}

.btn-primary:hover:not(:disabled) {
  background: var(--color-primary-dark);
}

/* Secondary: Ë£úÂä©„Ç¢„ÇØ„Ç∑„Éß„É≥Ôºà„Ç≠„É£„É≥„Çª„É´Á≠âÔºâ */
.btn-secondary {
  background: transparent;
  color: var(--color-primary);
  border: 1px solid var(--color-outline);
}

.btn-secondary:hover:not(:disabled) {
  background: var(--color-surface-variant);
  border-color: var(--color-outline-variant);
}

/* Danger: Âç±Èô∫„Å™„Ç¢„ÇØ„Ç∑„Éß„É≥ÔºàÂâäÈô§Á≠âÔºâ */
.btn-danger {
  background: transparent;
  color: var(--color-error);
  border: 1px solid var(--color-error);
}

.btn-danger:hover:not(:disabled) {
  background: var(--color-error);
  color: white;
  border-color: var(--color-error);
}

.btn-success {
  background: #28a745;
  color: white;
}

.btn-success:hover:not(:disabled) {
  background: #218838;
}

.btn-info {
  background: #17a2b8;
  color: white;
}

.btn-info:hover:not(:disabled) {
  background: #138496;
}

.btn-warning {
  background: #fd7e14;
  color: white;
}

.btn-warning:hover:not(:disabled) {
  background: #e8590c;
}

/* === „Ç´„Éº„ÉâÂÖ±ÈÄö„Çπ„Çø„Ç§„É´ === */
.card {
  background: var(--color-surface);
  border: 1px solid var(--color-outline);
  border-radius: var(--radius-medium);
  padding: var(--spacing-lg);
  box-shadow: var(--elevation-1);
  transition: all var(--transition-normal);
}

.card:hover {
  box-shadow: var(--elevation-2);
  border-color: var(--color-outline-variant);
}

.card-header {
  margin-bottom: var(--spacing-md);
  padding-bottom: var(--spacing-sm);
  border-bottom: 1px solid var(--color-outline);
}

.card-title {
  margin: 0;
  font-size: 1.125rem;
  font-weight: 600;
  color: var(--color-on-surface);
}

.card-body {
  color: var(--color-on-surface-variant);
}

/* === „É¢„Éº„ÉÄ„É´ÂÖ±ÈÄö„Çπ„Çø„Ç§„É´ === */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  animation: fadeIn var(--transition-normal) ease-out;
}

.modal-content {
  background: white;
  border-radius: 8px;
  max-width: 90vw;
  max-height: 90vh;
  overflow-y: auto;
  box-shadow: var(--elevation-4);
  animation: slideIn var(--transition-slow) ease-out;
  position: relative;
}

.modal-header {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 16px 20px;
  background: var(--color-primary);
  color: white;
  position: relative;
}

.modal-header h2 {
  margin: 0;
  color: white;
  font-size: 20px;
  font-weight: 700;
  text-align: center;
}

.modal-body {
  padding: 20px;
}

.modal-footer {
  padding: 16px 20px;
  border-top: 1px solid var(--color-outline);
  display: flex;
  justify-content: flex-end;
  gap: 8px;
}

.close-button {
  position: absolute;
  top: 16px;
  right: 20px;
  background: rgba(255, 255, 255, 0.18);
  border: none;
  font-size: 18px;
  cursor: pointer;
  color: white;
  padding: 8px 12px;
  border-radius: 8px;
  transition: all 0.2s;
  font-weight: bold;
}

.close-button:hover {
  background: rgba(255, 255, 255, 0.28);
  transform: scale(1.05);
}

.close-button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

/* === „Ç¢„Éã„É°„Éº„Ç∑„Éß„É≥ === */
@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateY(-20px) scale(0.95);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

@keyframes modalSlideIn {
  from {
    opacity: 0;
    transform: translateY(-30px) scale(0.95);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

/* === „É°„ÉÉ„Çª„Éº„Ç∏Ë°®Á§∫ === */
.message {
  padding: 12px;
  border-radius: 6px;
  margin-bottom: 12px;
  font-size: 0.85em;
  text-align: center;
}

.message.success {
  background: #d4edda;
  color: #155724;
  border: 1px solid #c3e6cb;
}

.message.error {
  background: #f8d7da;
  color: #721c24;
  border: 1px solid #f5c6cb;
}

.message.warning {
  background: #fff3cd;
  color: #856404;
  border: 1px solid #ffeaa7;
}

.message.info {
  background: var(--color-info-light);
  color: var(--color-on-surface);
  border: 1px solid var(--color-info);
}

/* === „Çπ„Éö„Éº„Ç∑„É≥„Ç∞„É¶„Éº„ÉÜ„Ç£„É™„ÉÜ„Ç£ === */
.mt-0 { margin-top: 0; }
.mt-1 { margin-top: 0.25rem; }
.mt-2 { margin-top: 0.5rem; }
.mt-3 { margin-top: 1rem; }
.mt-4 { margin-top: 1.5rem; }

.mb-0 { margin-bottom: 0; }
.mb-1 { margin-bottom: 0.25rem; }
.mb-2 { margin-bottom: 0.5rem; }
.mb-3 { margin-bottom: 1rem; }
.mb-4 { margin-bottom: 1.5rem; }

.p-0 { padding: 0; }
.p-1 { padding: 0.25rem; }
.p-2 { padding: 0.5rem; }
.p-3 { padding: 1rem; }
.p-4 { padding: 1.5rem; }

/* === „Éï„É¨„ÉÉ„ÇØ„Çπ„É¶„Éº„ÉÜ„Ç£„É™„ÉÜ„Ç£ === */
.flex { display: flex; }
.flex-column { flex-direction: column; }
.flex-center { justify-content: center; align-items: center; }
.flex-between { justify-content: space-between; }
.flex-end { justify-content: flex-end; }
.gap-1 { gap: 0.25rem; }
.gap-2 { gap: 0.5rem; }
.gap-3 { gap: 1rem; }

/* === „ÉÜ„Ç≠„Çπ„Éà„É¶„Éº„ÉÜ„Ç£„É™„ÉÜ„Ç£ === */
.text-center { text-align: center; }
.text-left { text-align: left; }
.text-right { text-align: right; }
.font-bold { font-weight: 600; }
.font-normal { font-weight: 400; }

/* === Ë°®Á§∫„É¶„Éº„ÉÜ„Ç£„É™„ÉÜ„Ç£ === */
.hidden { display: none; }
.visible { display: block; }

