/* üå∏ „É°„Ç§„É≥„Ç¢„Éó„É™„Ç±„Éº„Ç∑„Éß„É≥ - „Éû„ÉÜ„É™„Ç¢„É´„Éá„Ç∂„Ç§„É≥ */
.main-app {
  display: flex;
  flex-direction: column;
  /* ÁîªÈù¢È´òÂõ∫ÂÆö„Çí„ÇÑ„ÇÅ„ÄÅÂøÖË¶Å„Å´Âøú„Åò„Å¶Á∏¶„Çπ„ÇØ„É≠„Éº„É´ÂèØËÉΩ„Å´ */
  min-height: 100vh;
  height: auto;
  background-color: var(--color-background);
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', sans-serif;
}

.main-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px 24px;
  background: #ffffff;
  border-bottom: 1px solid var(--color-outline);
  color: var(--color-on-surface);
  box-shadow: var(--elevation-header);
  position: relative;
  z-index: 10;
}

/* 1366x768Âêë„Åë: „Éò„ÉÉ„ÉÄ„Éº„Çí„Ç≥„É≥„Éë„ÇØ„Éà„Å´ */
@media (max-width: 1440px) and (max-height: 900px) {
  .main-header {
    padding: 12px 20px;
  }
  
  .main-header h1 {
    font-size: 1.5em;
  }
}

.main-header h1 {
  margin: 0;
  font-size: 1.8em;
  font-weight: 600;
  letter-spacing: 0.5px;
  display: flex;
  align-items: center;
  color: var(--color-header-text);
}

.main-header .header-logo {
  margin-right: 8px;
  vertical-align: middle;
  width: 32px;
  height: 32px;
  object-fit: contain;
  border-radius: 4px; /* optional subtle rounding */
  box-shadow: 0 0 0 0 rgba(0,0,0,0); /* placeholder for future */
}

/* Áµ±‰∏Ä: „Éú„Çø„É≥„Çπ„Çø„Ç§„É´ (Êóß‰∫åÈáçÂÆöÁæ©Èô§Âéª) */
.toggle-panel-button {
  background: #ffffff;
  color: var(--color-primary);
  border: 1px solid var(--color-outline-variant);
  padding: var(--spacing-sm) var(--spacing-md);
  border-radius: var(--radius-medium);
  cursor: pointer;
  font-size: 0.9em;
  font-weight: 500;
  transition: all 0.18s ease;
  -webkit-backdrop-filter: none;
  backdrop-filter: none;
}
.toggle-panel-button:hover {
  background: var(--color-primary);
  color: var(--color-on-primary);
  border-color: var(--color-primary);
}

.header-info {
  /* ÂÜçÁ¢∫Ë™ç: flexÁ∂≠ÊåÅ */
  display: flex;
  align-items: center;
  gap: var(--spacing-lg);
}

/* Ë™øÊï¥: ÁôΩËÉåÊôØ‰∏ä„Åß„Éú„Çø„É≥„ÅÆË¶ñË™çÊÄß„ÇíÂ∞ë„ÅóÊîπÂñÑ */
/* remove alternate white-on-blue variant */

/* Êóß: Ê∞¥Ëâ≤ËÉåÊôØ„Éê„ÉÉ„Ç∏ ‚Üí „Ç∑„É≥„Éó„É´„Å™„ÉÜ„Ç≠„Çπ„Éà„ÅÆ„Åø„Å∏ */
.system-info {
  background: none !important; /* „Éê„ÉÉ„ÇØ„Ç∞„É©„Ç¶„É≥„ÉâÈô§Âéª */
  color: var(--color-on-surface-variant); /* „Éî„É≥„ÇØ‰∏ä„Åß„Ç≥„É≥„Éà„É©„Çπ„ÉàÊúÄÈÅ©Âåñ */
  opacity: 0.9;
  padding: 0 !important; /* ‰ΩôÁôΩÊ∂à„Åó„Å¶Ë°åÈ´ò„ÅßÊèÉ„Åà„Çã */
  font-weight: 600;
  border: none;
  box-shadow: none;
}

.toggle-panel-button {
  background: var(--color-surface);
  color: var(--color-primary);
  border: 1px solid var(--color-primary);
  padding: var(--spacing-sm) var(--spacing-md);
  border-radius: var(--radius-medium);
  cursor: pointer;
  font-size: 0.9em;
  font-weight: 500;
  transition: all 0.2s ease;
}

.toggle-panel-button:hover {
  background: var(--color-primary);
  color: var(--color-on-primary);
}

.main-content {
  flex: 1;
  display: flex;
  /* „Ç´„É¨„É≥„ÉÄ„Éº„ÅåÈö†„Çå„Å™„ÅÑ„Çà„ÅÜ„Å´Á∏¶„Çπ„ÇØ„É≠„Éº„É´„ÇíË®±ÂèØ */
  overflow: auto;
}

.calendar-section {
  flex: 1;
  padding: 20px;
  /* ÂÜÖÈÉ®„ÅÆFullCalendar„ÇÑÊó•Âà•„ÉÜ„Éº„Éñ„É´„Åå„ÅØ„ÅøÂá∫„ÅôÂ†¥Âêà„Å´„Çπ„ÇØ„É≠„Éº„É´ÂèØ */
  overflow: auto;
  display: flex;
  flex-direction: column;
}

/* 1366x768Âêë„Åë: „Éë„Éá„Ç£„É≥„Ç∞„ÇíÂâäÊ∏õ„Åó„Å¶Ë°®Á§∫È†òÂüü„ÇíÁ¢∫‰øù */
@media (max-width: 1440px) and (max-height: 900px) {
  .calendar-section {
    padding: 16px;
  }
}

.side-panel-section {
  width: 360px;
  background: var(--color-surface);
  border-left: 1px solid var(--color-outline);
  box-shadow: var(--elevation-2);
  z-index: 100;
  /* „Çà„ÇäÂ§ß„Åç„ÅÑÁîªÈù¢„Åß„ÅØÂπÖ„ÇíÂ∫É„Åí„Çã */
}

@media (min-width: 1600px) {
  .side-panel-section {
    width: 420px;
  }
}

@media (min-width: 1920px) {
  .side-panel-section {
