.ledger-view {
  background: var(--color-surface);
  border-radius: var(--radius-medium);
  box-shadow: var(--elevation-1);
  display: flex;
  flex-direction: column;
  gap: 16px;
  padding: 20px;
}

@media (max-width: 1440px) and (max-height: 900px) {
  .ledger-view {
    gap: 12px;
    padding: 16px;
  }
}

.ledger-toolbar {
  align-items: center;
  display: flex;
  flex-wrap: wrap;
  gap: 16px;
}

@media (max-width: 1440px) and (max-height: 900px) {
  .ledger-toolbar {
    gap: 12px;
  }
}

.ledger-nav-buttons {
  display: inline-flex;
  gap: var(--spacing-sm);
}

.ledger-nav-buttons button,
.ledger-date-picker input[type="date"],
.ledger-filter-mine input[type="checkbox"] {
  font-size: 0.9rem;
}

.ledger-nav-buttons button,
.ledger-view button {
  background: var(--color-surface-variant);
  border: 1px solid var(--color-outline);
  border-radius: var(--radius-small);
  cursor: pointer;
  padding: 6px 12px;
  transition: background 0.2s ease;
}

.ledger-nav-buttons button:hover,
.ledger-view button:hover {
  background: var(--color-outline-variant);
}

@media (max-width: 1440px) and (max-height: 900px) {
  .ledger-nav-buttons button,
  .ledger-view button {
    font-size: 0.875em;
    padding: 5px 10px;
  }
}

.ledger-date-picker,
.ledger-filter-mine {
  display: inline-flex;
  align-items: center;
  gap: var(--spacing-xs);
  font-size: 0.9rem;
}

.ledger-table-wrapper {
  overflow-x: auto;
}

.ledger-table {
  border: 1px solid var(--color-outline);
  border-collapse: initial;
  border-spacing: 0;
  min-width: 720px;
  width: max(100%, 960px);
}

.ledger-table th,
.ledger-table td {
  border: 1px solid var(--color-outline-variant);
  padding: 8px;
  vertical-align: top;
  background: var(--color-surface);
  background-clip: padding-box;
}

@media (max-width: 1440px) and (max-height: 900px) {
  .ledger-table th,
  .ledger-table td {
    padding: 6px;
  }
}

@media (min-width: 1920px) {
  .ledger-table th,
  .ledger-table td {
    padding: 10px;
  }
}

.ledger-period-header {
  position: sticky;
  left: 0;
  z-index: 3;
  background: var(--color-surface-variant);
  box-shadow: 2px 0 4px rgba(0, 0, 0, 0.1);
  min-width: 110px;
}

.ledger-period-cell {
  position: sticky;
  left: 0;
  z-index: 2;
  background: var(--color-surface-variant);
  box-shadow: 2px 0 4px rgba(0, 0, 0, 0.1);
  min-width: 110px;
}

@media (max-width: 1440px) and (max-height: 900px) {
  .ledger-period-cell,
  .ledger-period-header {
    min-width: 100px;
  }
}

@media (min-width: 1600px) {
  .ledger-period-cell,
  .ledger-period-header {
    min-width: 130px;
  }
}

.period-label {
  font-size: 1rem;
  font-weight: 600;
}

.period-time {
  color: var(--color-on-surface-variant);
  font-size: 0.85rem;
  margin-top: 2px;
}

.ledger-room-header {
  background: var(--color-surface-variant);
  font-size: 0.85rem;
  min-width: 130px;
  text-align: center;
  white-space: nowrap;
}

@media (max-width: 1440px) and (max-height: 900px) {
  .ledger-room-header {
    font-size: 0.8rem;
    min-width: 130px;
  }
}

@media (min-width: 1600px) {
  .ledger-room-header {
    font-size: 0.9rem;
    min-width: 140px;
  }
}

.ledger-reservation-cell {
  height: 40px;
  min-width: 130px;
  padding: 3px;
  position: relative;
  vertical-align: top;
}

@media (max-width: 1440px) and (max-height: 900px) {
  .ledger-reservation-cell {
    height: 35px;
    min-width: 130px;
    padding: 2px;
  }
}

@media (min-width: 1600px) {
  .ledger-reservation-cell {
    height: 50px;
    min-width: 140px;
    padding: 4px;
  }
}

.ledger-empty {
  display: inline-block;
  color: var(--color-on-surface-variant);
  font-size: 0.8rem;
}

.ledger-cell-clickable {
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.ledger-cell-clickable:hover {
  background-color: var(--color-surface-variant);
}

.ledger-reservation-list {
  display: flex;
  flex-direction: column;
  gap: 2px;
  height: 100%;
  list-style: none;
  margin: 0;
  overflow-y: auto;
  padding: 0;
}

.ledger-reservation-button,
.ledger-reservation-item,
.ledger-reservation-list {
  display: flex;
  flex-direction: column;
}

.ledger-reservation-button {
  background: #fff;
  border: 1px solid var(--color-outline-variant);
  border-radius: 4px;
  box-shadow: 0 1px 2px rgba(15, 23, 42, 0.08);
  cursor: pointer;
  gap: 3px;
  padding: 4px 6px;
  text-align: left;
  transition: all 0.2s ease;
  width: 100%;
}

.ledger-reservation-button:hover {
  background: var(--color-surface-variant);
  box-shadow: 0 2px 4px rgba(15, 23, 42, 0.12);
  transform: translateY(-1px);
}

.ledger-reservation-button:active {
  transform: translateY(0);
}

.ledger-reservation-title {
  color: var(--color-on-surface);
  font-size: 0.65rem;
  font-weight: 600;
  line-height: 1.2;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.ledger-reservation-owner,
.ledger-reservation-title {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.ledger-reservation-owner {
  color: var(--color-on-surface-variant);
  font-size: 0.55rem;
  line-height: 1.1;
}

@media (max-width: 900px) {
  .ledger-view {
    padding: var(--spacing-md);
  }

  .ledger-period-cell,
  .ledger-period-header {
    min-width: 100px;
  }

  .ledger-reservation-cell,
  .ledger-room-header {
    min-width: 120px;
  }
}

@media (max-width: 600px) {
  .ledger-toolbar {
    flex-direction: column;
    align-items: flex-start;
  }

  .ledger-nav-buttons {
    width: 100%;
    justify-content: space-between;
  }

  .ledger-table {
    min-width: 640px;
  }
}
